import React                                        from 'react';
import { connect }                                  from 'react-redux';

/* ################################################### */
/*          ui framework         */
/* ################################################### */

import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import TextField                                    from 'material-ui/TextField';
import FlatButton                                   from 'material-ui/FlatButton';
import RaisedButton                                 from 'material-ui/RaisedButton';
import SearchIcon                                   from 'material-ui-icons/Search';
import MuiThemeProvider                             from 'material-ui/styles/MuiThemeProvider';
import FlatPagination                               from 'material-ui-flat-pagination';

/* ################################################### */
/*          redux         */
/* ################################################### */

import ReduxActionCustomerUserList, { ReduxEnumCustomerUserList } from '../../../_redux/action/reduxAction.customer.user.list';


/* ################################################### */
/*          helper         */
/* ################################################### */
import HelperNavigate , { HelperNavigateENUM }		from '../../_helper/h.navigate';
import HelperStorage		                        from '../../_helper/h.storage';




/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../_abstract/c.abstract';




/* ################################################### */
/*          component         */
/* ################################################### */

import ComponentCustomLoading                       from '../../_component/loading/cc.loading';

import '../customer_user_listing/s.customer.user.css';

export default connect(
    ( state ) => {

        let thisState = state[ 'ReduxCustomerUserList' ] ;
        let stateObj = { };

        if ( thisState ) {

            for ( let i = 0 ; i < thisState.length; i ++ ) {
                let recordItem = thisState[ i ];
                if ( !recordItem[ 'completed' ] ) {

                    if ( ReduxEnumCustomerUserList.INIT_DONE == recordItem[ 'type' ] ) {
                        stateObj[ 'onReadyFunction' ]       = 'requestInitDone';
                        stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                    } else if ( ReduxEnumCustomerUserList.LIST_DONE == recordItem[ 'type' ] ) {
                        stateObj[ 'onReadyFunction' ]       = 'requestListDone';
                        stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                    }


                    recordItem[ 'completed' ] = true;
                }
            }

        }

        return stateObj;
    }
) (
    class ComponentCustomerUserList extends ComponentAbstract {
    
    constructor( props ) {
        super( props )
        this.state = {
            isBegin         : true
            , isLoading       : false
            , result: []
            , form : {
                name : ''
                , email : ''
            }
            , pagination : {
                offset : 1
                , limit : 20
                , total : 100
            }
        }
    }

    /*
        ################################################################################################################################################
        ################################################################################################################################################
                listener
        ################################################################################################################################################
        ################################################################################################################################################
    */

       _onReady() {
        console.log( 'ComponentCustomerUserList : _onReady : *** ');

        if ( this.state.isBegin ) {

            console.log( this.state.isBegin );

            this.setState( { isBegin : false }, () => {
                this.requestList();
            } )

        }

    }

    /*
        ################################################################################################################################################
        ################################################################################################################################################
                event
        ################################################################################################################################################
        ################################################################################################################################################
    */

    _handleChange( key , value ) {
        let form = this.state.form;
        form[ key ] = value;
        this.setState( {
            form : form
        } )
    }

    onClickSearch() {

    }

     /*
        ################################################################################################################################################
        ################################################################################################################################################
                request
        ################################################################################################################################################
        ################################################################################################################################################
    */
    requestList( ) {
        console.log( 'ComponentCustomerUserList : requestList : ***' )

        Promise.all( [
            ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
            , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
        ] ).then(
            ( value ) => {

                this.setState(
                    { isLoading: true }
                    , () => {
                        this.dispatch(
                            ReduxActionCustomerUserList.rList(
                                { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                , {}
                            )
                        )
                    }
                );

            }
        )

    }

    requestListDone( result ) {
        console.log( 'ComponentCustomerUserList : requestListDone : *** : result => ', result );

        let pagination = this.state['pagination'];
        pagination['total'] = result.length / pagination['limit'];

        this.setState( {
            isLoading: false
            , result : result
            , pagination : pagination
        } );
        this.dispatch(
            ReduxActionCustomerUserList.rRequestEnd()
        );
    }

    /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */
    render() {
        return (
            <div>
                <h4 style={ { paddingLeft: 25 } }>Customer User Listing</h4>

                <div style={ { paddingLeft: 25, paddingBottom: 10  } }>

                    <div style={ { display: 'flex' , justifyContent: 'flex-start' } } >

                        <div style={ { flex: 0.2 } }>

                            <p style={ { paddingTop: 12 } }>Customer Name</p>

                        </div>

                        <div style={ { flex: 0.3 } }>

                            <TextField 
                                hintText="alphabets only"
                                value={ this.state.form.name }
                                onChange={ ( event ) => {
                                    this._handleChange( 'name' , event.target.value );
                                } } />

                        </div>

                    </div>

                    <div style={ { display: 'flex' , justifyContent: 'flex-start' } } >

                        <div style={ { flex: 0.2 } }>

                            <p style={ { paddingTop: 12 } }>Email</p>

                        </div>

                        <div style={ { flex: 0.3 } }>

                            <TextField 
                                hintText="e.g. customer@gmail.com"
                                value={ this.state.form.email }
                                onChange={ ( event ) => {
                                    this._handleChange( 'email' , event.target.value );
                                } } />

                        </div>

                    </div>

                    <div style={ { display: 'flex' , justifyContent: 'flex-start', marginTop: 10 } }>
                        <div style={ { flex: 0.2 } }/>
                        <RaisedButton
                                        label={ "Search" }
                                        onClick={ () => {
                                            this.onClickSearch();
                                        } }
                            />

                    </div>

                    <hr/>

                    <div style={ { marginTop : '40px' } } >

                        <div style={ { display: 'flex' , justifyContent: 'center' } }>
                            
                            <MuiThemeProvider>
                                <FlatPagination
                                offset={ this.state.pagination.offset }
                                limit={ this.state.pagination.limit }
                                total={ this.state.pagination.total }
                                nextPageLabel={ 'Next' }
                                previousPageLabel={ 'Previous' }
                                otherPageLabelStyle={ {
                                    fontSize : '16px'
                                } }
                                currentPageLabelStyle={ {
                                    fontSize : '16px'
                                } }
                                onClick={ (e, offset) => { 
                                    let pagination = this.state.pagination;
                                    pagination[ 'offset' ] = offset;
                                    this.setState( { pagination : pagination } )
                                } }
                                />
                            </MuiThemeProvider>


                        </div>

                        <div style={ { marginTop : '10px' } }>

                            <Table>
                                <TableHeader displaySelectAll={ false }>
                                    <TableRow>
                                        <TableHeaderColumn className={ ' cust-user-backgroundColor-red cust-user-textAlign-center cust-user-color-white ' }>Customer Name</TableHeaderColumn>
                                        <TableHeaderColumn className={ ' cust-user-backgroundColor-red cust-user-textAlign-center cust-user-color-white ' }>IC Number</TableHeaderColumn>
                                        <TableHeaderColumn className={ ' cust-user-backgroundColor-red cust-user-textAlign-center cust-user-color-white ' }>Mobile Number</TableHeaderColumn>
                                        <TableHeaderColumn className={ ' cust-user-backgroundColor-red cust-user-textAlign-center cust-user-color-white ' }>Email</TableHeaderColumn>
                                    </TableRow>
                                </TableHeader>
                                <TableBody>
                                    {
                                        this.state.result.map( ( item, key ) => {
                                            return (
                                                <TableRow key={ item.customerId } >
                                                    <TableRowColumn> { item.name } </TableRowColumn>
                                                    <TableRowColumn> { item.idNumber } </TableRowColumn>
                                                    <TableRowColumn> { item.mobileNumber } </TableRowColumn>
                                                    <TableRowColumn> { item.email } </TableRowColumn>
                                                </TableRow> 
                                            );                                            
                                        })
                                    }
                                </TableBody>
                            </Table>;

                        </div>

                        <div className={ ' cust-user-textAlign-center ' } >

                            <RaisedButton
                                                className={ 'cust-user-margin-10' }
                                                label={ "Print" }
                                                onClick={ () => {

                                                } } />


                            <RaisedButton
                                                className={ 'cust-user-margin-10' }
                                                label={ "Download CSV" }
                                                onClick={ () => {

                                                } } />

                            <RaisedButton
                                                className={ 'cust-user-margin-10' }
                                                label={ "Download PDF" }
                                                onClick={ () => {

                                                } } />

                            </div>

                    </div>

                </div>  

                    <ComponentCustomLoading
                        visible={ this.state.isLoading }
                    />
            </div>
        )
    }
}
)


